<template>
  <div>
    <div>但是都是极好的</div>
    <el-amap style="width: 100%; height: 600px;" vid="amap" :plugin="plugin" :events="events" class="amap" :center="center" :zoom="zoom">
      <el-amap-marker v-for="marker in markers" :key="marker.mapName" :position="marker.position" :content="marker.content" :visible="true" :draggable="false"></el-amap-marker>
    </el-amap>
  </div>
</template>

<script>
import Vue from 'vue';
import VueAMap from 'vue-amap';


Vue.use(VueAMap);
VueAMap.initAMapApiLoader({
  key: '34cf83b5ae75f578aab023529efa79e7',
  plugin: ['AMap.Autocomplete', 'AMap.PlaceSearch', 'AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType', 'AMap.PolyEditor', 'AMap.CircleEditor', 'AMap.Geolocation', 'AMap.Geocoder', 'AMap.Map', 'AMap.LngLat', 'AMap.Icon'],
  v: '1.4.4'
});
export default {
  name: 'app',
  data () {
    let self = this;
    return {
      nearbyInfo: [], // 周边信息---高德反馈（周边建筑信息）
      addressInfo: '', // 城市信息---高德反馈（省市区、adcode）
      center: [113.94711,22.5531], // 高德地图中心点
      zoom: 100, // 地图缩放
      markers: [],
      events: {
        'click': (e) => { // 点击地图的时候，获取点击的经纬度，并将地图中心点移自此处
          let m = e.lnglat;
          self.addrInput = '';
          self.center = [m.lng, m.lat];
          self.GDmapGetInfoOfNearby(m.lng, m.lat, self); // 获取周边信息
        }
      },
      plugin: [
        {
          pName: 'Geolocation',
          noIpLocate: 1, // ios11： 禁止ip定位：ios11之后默认是ip定位，参数为1则是禁止ip定位
          events: {
            init: (o) => {
              self.GDinit(o, self); // 获取当前位置
              self.MyMap()
            }
          }
        }
      ]
    };
  },
  mounted() {
    // this.MyMap()
    this.changeMap()
	},
  methods: {
    /* 初始化定位当前位置 */
    GDinit (o, self) {
      o.getCurrentPosition((status, result) => {
        // console.log(status, result);
        if (status === 'complete' && result.info === 'SUCCESS') {
          let lat = result.position.lat;
          let lng = result.position.lng;
          self.center = [lng, lat];
          self.GDmapGetInfoOfNearby(lng, lat, self); // 获取周边信息
        }
      });
    },
    /* 获取周边建筑信息 */
    GDmapGetInfoOfNearby (lng, lat, self) {
      // 这里通过高德 SDK 完成。
      let geocoder = new AMap.Geocoder({ 
        radius: 1000,
        extensions: 'all',
        poitype: ''
      });
      geocoder.getAddress([lng, lat], (status, result) => {
        if (status === 'complete' && result.info === 'OK') {
          if (result && result.regeocode) {
            console.log(result.regeocode.pois[0]);
            self.$nextTick();
          }
        }
      });
    },
    MyMap() {
      this.markers = [
        {
          position: [113.946809,22.553399],
          content: this.getContentHtml('庆邦电子大厦法定法定放'),
          contry: '中国',
        },
        {
          position: [113.94518, 22.552204],
          content: this.getContentHtml('科陆大厦'),
          contry: '中国',
        },
        {
          position: [113.944763,22.55415],
          content: this.getContentHtml('意中利科技园'),
          contry: '中国',
        },
      ]
    },
    getContentHtml(content) {
      // 未避免被简书转化未设置img的src属性
      return '<p><img width="20px" class="type" src="https://webapi.amap.com/theme/v1.3/markers/n/mark_bs.png"></p><p class="type" data="1" style="position: absolute;width: 100px;left: -44px;top: 34px;background:rgba(255,255,255,1);border:3px solid rgba(36,189,93,1);box-shadow:-2px 3px 9px 1px rgba(73,78,84,0.5);border-radius:10px;color:rgba(67,67,67,1);font-size: 12px;word-break: normal;">'+ content + '</p>';
    },
    changeMap (content){
      addEventListener('click',function(e){
        console.log(e.target.className)
        console.log(e.target.getAttribute('data'))
      })
	  }
  }
}
</script>

<style lang="scss">
</style>



