import Vue from 'vue';
import VueRouter from 'vue-router';
import as from './aaa.vue';

Vue.use(VueRouter); 


const routes = [
    {
        path: '/aaa',
        component: as,
    }
];

const router = new VueRouter({
    routes
});
export default router;


